import React from "react"
import { Link } from "gatsby"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {kebabCase, toLower} from "lodash"

const PostsListCard = ({ frontmatter, fields, excerpt }) => {
  const title = frontmatter.title || fields.slug
  const tags = frontmatter.tags;

  return (
    <div className="mt-4 mb-4 pt-4 pb-4">
      <h4 className="card-title mb-2">
        <Link
          style={{ boxShadow: `none`, textDecoration: `none`, color: `#17a2b8` }}
          to={`/${fields.slug}/`}>{title}</Link>
      </h4>
      <div className="d-flex align-items-center mb-1">
        <div className="mr-2" style={{fontSize: '13px', color: '#666666'}}>{frontmatter.date}</div>
        {
          (tags && tags.length > 0) &&
          tags.map(tag => (
            <Link
                to={`/tags/${kebabCase(toLower(tag))}`}
                className="badge badge-info pt-1 pb-1 pr-2 pl-2 ml-1"
                key={tag}
              >
              <FontAwesomeIcon icon={'tag'} size="xs" className={"mr-1"} />
              {tag}
            </Link>
          ))
        }
      </div>
      <div
        style={{color: '#999999'}}
        dangerouslySetInnerHTML={{
          __html: frontmatter.description || excerpt,
        }}
      />
    </div>
  )
}

export default PostsListCard
